<template>
  <div class="social-sharing">
    <p v-if="!isServer">
      <span>{{ $t("share_product_on") }}</span>
      <component
        :is="network.component"
        v-for="network in networks"
        :key="network.network"
        :share-options="network.shareOptions"
        style="cursor: pointer; aspect-ratio: 2/1"
        @click="
          () => {
            if (network.network == 'sms') openSocialLink(`sms:?body=${network.shareOptions.body}`);
            if (network.network == 'messenger') {
              openSocialLink(`fb-messenger://share/?link=${getFullPath}`);
            }
          }
        "
      >
        <span class="lnr lnr-envelope" v-if="network.iconType == 'lnr'"></span>
        <font-awesome-icon :icon="[network.iconType, network.icon]" v-else />
      </component>
    </p>
  </div>
</template>

<script setup>
import { computed } from "vue";
import { useRoute } from "vue-router";
import config from "@config";
import {
  SEmail,
  SFacebook,
  SLinkedIn,
  SPinterest,
  SSms,
  STelegram,
  STwitter,
  SWhatsApp,
} from "vue-socials";
import { isServer } from "@storefront/core/helpers";

const route = useRoute();
const props = defineProps({
  product: {
    type: Object,
    required: true,
  },
});

const getFullPath = computed(() => config.app_url + "/" + route.fullPath);
const getHashTags = computed(() =>
  props.product.meta_keyword ? props.product.meta_keyword.replace(/\s*,\s*/g, ",") : ""
);

const networks = [
  {
    network: "email",
    name: "Email",
    iconType: "lnr",
    icon: "lnr-envelope",
    shareOptions: {
      mail: "",
      subject: props.product.name,
      body: [props.product.meta_description, getFullPath.value].concat(" "),
    },
    component: SEmail,
  },
  {
    network: "facebook",
    name: "Facebook",
    iconType: "fab",
    icon: "facebook",
    shareOptions: {
      url: getFullPath.value,
      quote: props.product.meta_description,
      hashtag: getHashTags.value,
      title: props.product.name,
    },
    component: SFacebook,
  },
  {
    network: "linkedin",
    name: "LinkedIn",
    iconType: "fab",
    icon: "linkedin",
    shareOptions: {
      url: getFullPath.value,
    },
    component: SLinkedIn,
  },
  {
    network: "messenger",
    name: "Messenger",
    iconType: "fab",
    icon: "facebook-messenger",
    shareOptions: {},
    component: SSms,
  },
  {
    network: "pinterest",
    name: "Pinterest",
    iconType: "fab",
    icon: "pinterest",
    shareOptions: {
      url: getFullPath.value,
      media: "",
      description: [props.product.name, props.product.meta_description, getFullPath.value].concat(
        " "
      ),
    },
    component: SPinterest,
  },
  {
    network: "sms",
    name: "SMS",
    iconType: "fas",
    icon: "comment-dots",
    shareOptions: {
      number: "+1(999)999-99-99",
      body: [props.product.name, props.product.meta_description, getFullPath.value].concat(" "),
    },
    component: SSms,
  },
  {
    network: "telegram",
    name: "Telegram",
    iconType: "fab",
    icon: "telegram-plane",
    shareOptions: {
      url: getFullPath.value,
      text: [props.product.name, props.product.meta_description, getFullPath.value].concat(" "),
    },
    component: STelegram,
  },
  {
    network: "twitter",
    name: "Twitter",
    iconType: "fab",
    icon: "twitter",
    shareOptions: {
      url: getFullPath.value,
      text: [props.product.name, props.product.meta_description, getFullPath.value].concat(" "),
      hashtags: getHashTags.value,
      via: "twitterdev",
    },
    component: STwitter,
  },
  {
    network: "whatsapp",
    name: "Whatsapp",
    iconType: "fab",
    icon: "whatsapp",
    shareOptions: {
      number: "",
      text: [props.product.name, props.product.meta_description, getFullPath.value].concat(" "),
    },
    component: SWhatsApp,
  },
];

function openSocialLink(url) {
  window.open(url);
}
</script>
