<template>
  <div class="ratings d-none d-md-flex align-items-center">
    <div class="price">{{ reviewData.name }}:</div>
    <div class="stars">
      <ul class="ratings-star">
        <li
          v-for="(isFill, idx) of rateStars"
          :key="idx"
          :class="isFill ? 'text-yellow' : 'text-gray'"
        >
          <i class="lnr lnr-star" @click="clickStar(idx)"></i>
        </li>
      </ul>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from "vue";

const props = defineProps({
  reviewData: {
    type: Object,
    required: true,
  },
});

const emit = defineEmits(["input"]);

const stars = ref(0);

const rateStars = computed(() => {
  return new Array(5).fill(false).map((_, i) => i < stars.value);
});

const clickStar = (idx) => {
  stars.value = parseInt(idx + 1);
  const found = props.reviewData.values.find((v) => {
    return parseInt(v.value) === stars.value;
  });
  if (found != null) {
    emit("input", found.value_id);
  }
};

// Expose the resetStars method to the parent
const resetStars = () => {
  stars.value = 0;
};

defineExpose({ resetStars });
</script>
