<template>
  <div class="ratings d-none d-md-flex align-items-center">
    <div class="price">{{ title }}</div>
    <ul class="ratings-star">
      <li
        v-for="(isFill, idx) of rateStars"
        :key="idx"
        :class="isFill ? 'text-yellow' : 'text-gray'"
      >
        <i class="lnr lnr-star"></i>
      </li>
    </ul>
  </div>
</template>

<script setup>
import { computed } from "vue";

const props = defineProps({
  stars: {
    type: Number,
    required: true,
  },
  title: {
    type: String,
    required: false,
  },
});

const rateStars = computed(() => {
  return new Array(5).fill(false).map((_, i) => i < props.stars);
});
</script>
