<template>
  <div class="alert-block">
    <BAlert
      v-model="showDismissibleAlert"
      class="d-flex align-items-center"
      :variant="product.alertVariant || 'success'"
      dismissible
    >
      <div v-if="isInfoAlert" class="alert-content">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="16"
          fill="currentColor"
          class="bi bi-bell"
          viewBox="0 0 16 16"
        >
          <path
            d="M8 16a2 2 0 0 0 2-2H6a2 2 0 0 0 2 2M8 1.918l-.797.161A4 4 0 0 0 4 6c0 .628-.134 2.197-.459 3.742-.16.767-.376 1.566-.663 2.258h10.244c-.287-.692-.502-1.49-.663-2.258C12.134 8.197 12 6.628 12 6a4 4 0 0 0-3.203-3.92zM14.22 12c.223.447.481.801.78 1H1c.299-.199.557-.553.78-1C2.68 10.2 3 6.88 3 6c0-2.42 1.72-4.44 4.005-4.901a1 1 0 1 1 1.99 0A5 5 0 0 1 13 6c0 .88.32 4.2 1.22 6"
          />
        </svg>
        <span>Let op! probeer het nog eens</span>
      </div>
      <div v-if="isDangerAlert" class="alert-content">
        <i class="lnr lnr-cross-circle"></i>
        <span>Mislukt, probeer het nog eens</span>
      </div>
      <div v-if="isWarningAlert" class="alert-content">
        <i class="lnr lnr-warning"></i>
        <span>Waarschuwing</span>
      </div>
      <div
        v-if="isSuccessAlert || (!isWarningAlert && !isInfoAlert && !isDangerAlert)"
        class="d-flex justify-content-between align-items-center alert-content"
      >
        <div class="left-content">
          <i class="lnr lnr-checkmark-circle"></i>
          <span>{{ product.name }}</span>
          <span> aan je</span>
          <BLink href="#" class="content-link"> verlanglijstje.</BLink>
        </div>
        <BLink href="#" class="btn alert-btn ml-20">Wensenlijst</BLink>
      </div>
    </BAlert>
  </div>
</template>

<script setup>
import { computed, defineModel } from "vue";

const props = defineProps(["product"]);
const showDismissibleAlert = defineModel();

const isInfoAlert = computed(() => props.product.alertVariant === "info");
const isDangerAlert = computed(() => props.product.alertVariant === "danger");
const isWarningAlert = computed(() => props.product.alertVariant === "warning");
const isSuccessAlert = computed(() => props.product.alertVariant === "success");
</script>
