<template>
  <div v-if="isClient">
    <BModal
      id="ProductCardModal"
      okVariant="success"
      okTitle="Doorgaan met afrekenen"
      cancelTitle="Doorgaan met winkelen"
      :title="$t('items_added_to_cart', { numberOf: quantity })"
      @ok="clickOk"
      @cancel="clickCancel"
      v-slot="{ visible }"
      :key="quantity"
    >
      <img :src="product.image.large" v-if="product.image.large" />
      <p class="my-4">{{ product.name }}</p>
      <p class="my-price">
        &euro;{{ priceRange.maximum_price.final_price.value.toFixed(2).replace(".", ",") }}
      </p>
      <section class="more-products product-section" v-if="products && products.length > 0">
        <div class="container">
          <span class="section-title d-block font-weight-normal text-center"
            >Klanten bestelden ook</span
          >
          <MoreProductsCarousel
            class="pt-5 in-modal"
            :products="products"
            type="modal"
            v-if="visible"
          />
        </div>
      </section>
    </BModal>
  </div>
</template>

<script setup>
import { onMounted, ref } from "vue";
import { useRouter } from "vue-router";
import MoreProductsCarousel from "../MoreProductsCarousel.vue";

const isClient = ref(false);

defineProps({
  product: Object,
  priceRange: Object,
  quantity: Number,
  products: Array,
});

const router = useRouter();
const clickOk = () => {
  router.push("/checkout");
};
const clickCancel = () => {
  window.scrollTo(0, 0);
};

onMounted(() => {
  isClient.value = true;
});
</script>

<style>
.in-modal {
  .available-sizes-list {
    flex-grow: 1;
    height: auto !important;
  }
}
</style>
