<template>
  <b-tabs active-nav-item-class="active-opener" class="product--description-tab">
    <b-tab active title="Omschrijving">
      <div v-html="product.description.html"></div>
      <div class="d-block sku">SKU: {{ product.sku }}</div>
    </b-tab>
    <b-tab title="Specificaties">
      <div v-for="(info, index) in product.information_attributes" :key="index" class="d-flex mb-4">
        <span v-if="info.value != 'false'" class="category d-block">{{ info.label }}:</span>
        <span v-if="info.value != 'false'" class="detail ml-20 d-block">{{
          capitalizeFirst(info.value)
        }}</span>
      </div>
    </b-tab>
    <b-tab :key="product.sku" :title="`Beoordelingen (${product.review_count})`">
      <ProductReview></ProductReview>
    </b-tab>
  </b-tabs>
</template>

<script setup>
import { computed } from "vue";
import { useStore } from "vuex";
import ProductReview from "@/base/core/components/ProductReview";

const store = useStore();

const product = computed(() => store.getters["product/getCurrentProduct"]);

const capitalizeFirst = (input) => {
  let lower = input.toLowerCase();
  return lower.charAt(0).toUpperCase() + lower.slice(1);
};
</script>
