<template>
  <div class="ProductReview" v-if="!isServer">
    <b-modal
      id="modal-review"
      okVariant="success"
      okTitle="Ok"
      :title="$t('review_send')"
      @ok="clickReviewOk"
    >
      <p class="my-4">{{ $t("review_thankyou") }}</p>
    </b-modal>

    <h3>{{ $t("productreview") }}</h3>
    <div class="review-block">
      <div class="row">
        <div class="col-md-12">
          <div v-if="product != null" class="product_rating">
            <div v-if="product.reviews.items.length == 0">
              {{ $t("no_reviews") }}
            </div>
            <div v-for="(rev, index) of product.reviews.items" :key="index">
              <div v-for="(rat, idx) of rev.ratings_breakdown" :key="idx">
                <ProductRating
                  :title="rat.name"
                  :stars="parseInt(rat.value)"
                ></ProductRating>
              </div>
              <div class="nickname">{{ rev.nickname }}</div>
              <div class="summary">{{ rev.summary }}</div>
              <div class="review">{{ rev.text }}</div>
            </div>
          </div>
          <h4>{{ $t("you'rereviewing") }}</h4>
          <div
            v-for="(ritem, idx) of reviewMeta"
            :key="idx"
            class="review_rating"
          >
            <ReviewRating
              ref="reviewRatings"
              :reviewData="ritem"
              @input="(value_id) => (ratings[idx] = value_id)"
            ></ReviewRating>
          </div>
          <div id="Nickname" role="group" class="form-group review-inputs">
            <label id="first-Nickname" for="First Name" class="d-block">{{
              $t("nickname")
            }}</label>
            <div class="no-focus-ring">
              <input
                id="first_name"
                type="text"
                class="form-control"
                aria-describedby="firstnickname-Review"
                v-model="nickname"
              />
              <div id="firstnickname-review" class="invalid-nickname"></div>
            </div>
          </div>
          <div id="Summary" role="group" class="form-group review-inputs">
            <label id="Summary" for="Product Summary" class="d-block">{{
              $t("summary")
            }}</label>
            <div class="no-focus-ring">
              <input
                id="Summary"
                type="text"
                class="form-control"
                aria-describedby="Summary-Review"
                v-model="summary"
              />
              <div id="Summary-review" class="invalid-Summary"></div>
            </div>
          </div>
          <div id="Review" role="group" class="form-group review-inputs">
            <label id="Review" for="Product Review" class="d-block">{{
              $t("review")
            }}</label>
            <div class="no-focus-ring">
              <textarea
                name="Review"
                cols="40"
                rows="5"
                v-model="text"
              ></textarea>
              <div id="review" class="invalid-Review"></div>
            </div>
          </div>
          <button @click="sendReview" class="submit reviewbutton">
            {{ $t("submitreview") }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from "vue";
import { useStore } from "vuex";
import ProductRating from "@/base/core/components/ProductRating";
import ReviewRating from "@/base/core/components/ReviewRating";
import { createProductReview } from "@storefront/core/data-resolver/products";
import { useModal } from "bootstrap-vue-next";
import { isServer } from "@storefront/core/helpers";
import { useI18n } from "vue-i18n";

const { show, hide } = useModal("modal-review");

const store = useStore();
const { t } = useI18n();

const nickname = ref("");
const summary = ref("");
const text = ref("");
const ratings = ref([]);
const test = ref(null);
const reviewRatings = ref([]);

const product = computed(() => store.getters["product/getCurrentProduct"]);
const reviewMeta = computed(
  () => store.getters["product/getProductReviewRatingsMetadata"]
);

const sendReview = async () => {
  let isValid = true;

  document.getElementById("firstnickname-review").textContent = "";
  document.getElementById("Summary-review").textContent = "";
  document.getElementById("review").textContent = "";

  if (!nickname.value.trim()) {
    document.getElementById("firstnickname-review").textContent =
      t("nickname_required");
    isValid = false;
  }

  if (!summary.value.trim()) {
    document.getElementById("Summary-review").textContent =
      t("summary_required");
    isValid = false;
  }

  if (!text.value.trim()) {
    document.getElementById("review").textContent = t("review_required");
    isValid = false;
  }

  if (!isValid) {
    return;
  }

  // Create ratings array
  const ratingsArray = [];
  reviewMeta.value.forEach((element, index) => {
    if (typeof ratings.value[index] != "undefined") {
      let obj = { id: element.id, value_id: ratings.value[index] };
      ratingsArray.push(obj);
    }
  });

  const inp = {
    sku: product.value.sku,
    nickname: nickname.value,
    summary: summary.value,
    text: text.value,
    ratings: ratingsArray,
  };

  test.value = inp;

  const retval = await createProductReview(inp, store);

  if (retval !== false) {
    show();
    nickname.value = "";
    summary.value = "";
    text.value = "";
    ratings.value = [];
    // ratings.value.forEach((element, index) => {
    //   ratings.value[index] = null;
    //   element = null;
    // });
    reviewRatings.value.forEach((child) => {
      if (child && typeof child.resetStars === "function") {
        child.resetStars();
      }
    });
  }
};

const clickReviewOk = () => {
  hide();
};
</script>
